const i18n = {
  'zh-cn': {
    init: "",
    counts: "",
    login: "",
    logout: "",
    leave: "",
    styling: "Markdown",
    write: "",
    preview: "",
    noPreview: "",
    submit: "",
    reply: "",
    loadMore: "",
    loading: "...",
    loadEnd: "",
    published: ""
  },
  en: {
    init: "Initialize A Issue",
    counts: "comments",
    login: "Login",
    logout: "Logout",
    leave: "Leave a comment",
    styling: "Styling with Markdown is supported",
    write: "Write",
    preview: "Preview",
    noPreview: "Nothing to preview",
    submit: "Submit",
    reply: "Reply",
    loadMore: "Load More",
    loading: "loading...",
    loadEnd: "Load completed",
    published: "Published on"
  }
};

export default function (lang) {
  const langObj = i18n[lang] || i18n["zh-cn"];
  return function (key) {
    return langObj[key] || `Unmath key: ${key}`;
  };
}
